import { Component, OnInit } from '@angular/core';
import { Environment, GoogleMap, GoogleMapOptions, GoogleMaps, GoogleMapsEvent, Marker } from '@ionic-native/google-maps';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.page.html',
  styleUrls: ['./contact.page.scss'],
})
export class ContactPage implements OnInit {
  map: GoogleMap;
  slideImgs = ['../../../assets/images/slides/quinoa.jpg'];
  pointToSells = [
    {
      name: 'Deliroots',
      street: 'Av. ferrocarril',
      number: '#33',
      suburb: 'El cerrito',
      state: 'Querétaro',
      zipCode: '',
      lat: 20.601493,
      lng:  -100.392938
    }
  ];

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.loadMap();
  }

  loadMap() {

    const mapOptions: GoogleMapOptions = {
      camera: {
         target: {
          lat: 20.601493,
          lng:  -100.392938
         },
         zoom: 6,
         tilt: 30
       }
    };

    this.map = GoogleMaps.create('map_canvas', mapOptions);
    for (const point of this.pointToSells) {
      const marker: Marker = this.map.addMarkerSync({
        title: point.name,
        icon: 'green',
        animation: 'DROP',
        position: {
          lat: point.lat,
          lng:  point.lng
        }
      });
      marker.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
          console.log('clicked');
      });
    }
  }

}
